.home select{
    min-width: 200px;
    color: black;
    width:300px;
    border: none;
    -webkit-appearance: none;
    padding: 7px 30px 7px 12px;
    border: 2px solid #E8EAED;
    border-radius: 5px;
    background: white;
    box-shadow: 0 1px 3px -2px #9098A9;
    cursor: pointer;
    font-family: inherit;
    font-size: 26px;
    transition: all 1500ms ease;

}
option{
    color: black;
    border: 100px;
    
    &[value=""][disabled]{
        display: none;
    }
    &:focus{
      outline: none;
      border-color: white;
      box-shadow: 0 0 0 2px rgba(#0077FF,.2);
    }
}